<template>
  <div class="tab-navigation">
    <tab-navigation-item v-for="(item, index) in list" :key="index">
      <div
        class="item"
        @click="goto(item, index)"
        :class="index === currentIndex ? 'activeClass' : ''"
      >
        <!-- <p style="width: 60px"></p> -->
        <p>{{ item.name }}</p>
      </div>
    </tab-navigation-item>
  </div>
</template>

<script>
import TabNavigationItem from "./TabNavigationItem.vue";
import request, { auth, headers } from "/src/pages/internal/network/request";
import { $getProviderByID } from "/src/pages/internal/network/providerTools/provider";
export default {
  name: "e-TabNavigation",
  props: {
    list: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  data() {
    return {
      currentIndex: 2,
    };
  },
  created() {
    // console.log('tab navigation this(to examine index default to be 2 ): ',this)
  },
  methods: {
    async goto(item, index) {
      console.log("tab-navigation goto item: ", item, "index: ", index);
      let name = item.name || "unnamed";
      if (name == "主理人设置") {
        let response_temp = await request({
          method: "POST",
          url: "/api/e9b849a515a84327b424af7ccdbf2949/v1_0_0/provider/insurance/info/get",
          data: {
            provider_id: Number(window.localStorage.getItem("providerID")),
          },
          auth,
          headers,
        });
        // console.log(' before ender 主理人设置: ',response_temp);
        if (!response_temp.details) {
          this.$toast.warn(
            "基本信息未通过审核，无法进入主理人设置",
            0,
            500,
            3000
          );
          return;
        }
        // console.log('通过主理人设置 路由守卫')
      }
      if (name == "商品发布") {
        let res2 = await $getProviderByID({
          user_id: Number(window.localStorage.getItem("userID")),
        });
        // console.log('get provider by userid id:',Number(window.localStorage.getItem("userID")))
        // console.log('进入商品发布之前的 主理人设置信息：',res2)
        if (!res2.details || res2.details.name == "尚未设置") {
          this.$toast.warn("主理人设置未通过审核，无法商品发布", 0, 500, 3000);
          return;
        }
        console.log("通过商品发布设置 路由守卫");
      }
      this.currentIndex = index;
      window.localStorage.setItem("currentIndex010006", index);
      this.$emit("resetBasicFrameIndex");
      this.$router.replace(item.path).catch((err) => {});
    },
  },
  components: {
    TabNavigationItem,
  },
};
</script>

<style scoped>
.tab-navigation {
  margin-top: 80px;
}
.item {
  /* width: 100%; */
  height: 60px;
  font-size: 20px;
  line-height: 60px;
  /* font-weight: lighter; */
  border-radius: 10.7px;
  margin: 10px 10px 10px 10px;

  display: flex;
  justify-content: center;
}
.item:hover {
  background: #e6e6e6;
}
.activeClass {
  color: #fff;
  font-weight: bold;
  background-color: var(--color-background-button-green);
}
.activeClass:hover {
  background: var(--color-background-button-green);
}
</style>
