<script>
// @ is an alias to /src
import TabNavigation from "../../../basicLayout//basicLayoutOfProviderTools/childComponents/tabNavigation/TabNavigation.vue";
import $ from "jquery";
import request from "../../../../internal/network/request";
export default {
  name: "e-ProviderBasicLayout",
  async created() {
    this.$nextTick(() => {
      if (!window.localStorage.getItem("currentIndex010006")) {
        // 说明是首次进入这个页面
        window.localStorage.setItem("currentIndex010006", 2);
      } else {
        // 刷新当前页面，本组件重新加载
        const index = parseInt(
          window.localStorage.getItem("currentIndex010006")
        );
        this.$refs.tabNavigation.currentIndex = index;
        if (index === -1) {
          this.index = -1;
        }
      }
    });
    // let userid = this.$store.state.userid
    // 设置消息队列
    {
      let userid = window.localStorage.getItem("userID");
      console.log("message page userid:");
      console.log(userid);
      let noti_res = await request({
        method: "POST",
        url: "/api/e9b849a515a84327b424af7ccdbf2949/mobile/common/v1_0_0/notify/all/active",
        data: {
          is_active: true, //总是true
          platform: 10003, // ALL = 10000, IOS = 10001, MINIAPP = 10002
          recipient_id: userid, // 接受公告user的id
        },
        auth: {
          username:
            "NginxAuthUsername16381efa069a90cbde99854c81183036a4bd2e4776eb770fa69740f1c5c06aa4",
          password:
            "NginxAuthPassworde0588e3b3c5b240fe3f5fb653d2990f34b52ee0b595acf6553a924b0716cb366",
        },
        headers: {
          "Content-Type": "application/json",
        },
      });
      console.log("noti res: ", noti_res);
      let gen_noti = noti_res.details.notify_generals;
      let user_noti = noti_res.details.notify_users;
      // console.log(gen_noti,user_noti)
      gen_noti = gen_noti.map((e) => {
        return {
          keyss: "gen_" + e.id,
          title: e.title,
          date: e.date_created,
          content: e.content.text || "",
          read: false,
        };
      });
      user_noti = user_noti.map((e) => {
        return {
          keyss: "user_" + e.id,
          title: e.title,
          date: e.date_created,
          content: e.content.text || "",
          read: e.status == 12004 ? true : false,
        };
      });
      let meslist = [...gen_noti, ...user_noti];
      this.meslist = meslist;
      let unreads = [];

      meslist.map((e) => {
        if (e.read == false) {
          unreads.push(e);
        }
      });
      console.log("noti unreads: ", unreads);
      this.unreads = unreads;
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.$bus.$on("setCurrentIndex010006To1", () => {
        this.index = 999;
        this.$refs.tabNavigation.currentIndex = 1;
      });
    });
  },
  data() {
    return {
      unreads: [],
      list: [
        {
          name: "全部信息",
          path: "/tools/provider/message/all",
        },
        {
          name: "系统公告",
          path: "/tools/provider/message/all",
        },
        {
          name: "公告通知",
          path: "/tools/provider/message/all",
        },
      ],
      index: 999,
      isProvider: true,
      infoShow: false,
      notiShow: false,
    };
  },
  methods: {
    reviewProgressClick() {
      this.index = -1;
      this.$refs.tabNavigation.currentIndex = -1;
      window.localStorage.setItem("currentIndex010006", -1);
      this.$router
        .replace("/tools/provider/progress/review")
        .catch((err) => {});
    },
    resetBasicFrameIndex() {
      this.index = 999;
    },
    logout() {
      window.localStorage.removeItem("list");
      window.localStorage.removeItem("order");
      window.localStorage.removeItem("currentIndex010006");

      window.localStorage.removeItem("userID");
      window.localStorage.removeItem("username");
      window.localStorage.removeItem("authToken");
      window.localStorage.removeItem("isStaff");
      window.localStorage.removeItem("isSuperUser");
      window.localStorage.removeItem("isProvider");
      window.localStorage.removeItem("providerID");

      /* this.cookieOperations.clearCookie('userId');
      this.cookieOperations.clearCookie('loginName');
      this.cookieOperations.clearCookie('authToken');
      this.cookieOperations.clearCookie('isStaff');
      this.cookieOperations.clearCookie('isSuperuser');
      this.cookieOperations.clearCookie('isProvider'); */

      this.$router.replace("/tools/login");
    },
    accountInfoShow() {
      this.infoShow = true;
      $(".accountArrow").css("transform", "rotate(180deg)");
    },
    accountInfoShut() {
      this.infoShow = false;
      $(".accountArrow").css("transform", "rotate(0deg)");
    },
    gotoAgreements() {
      this.list = [
        { name: "营探服务商费用核算与结算办法", path: "/ProviderAgreement1" },
        { name: "营探服务商管理办法", path: "" },
        { name: "营探服务商培训办法", path: "" },
