        { name: "营探服务商收费标准", path: "" },
        { name: "营探交易争议处理规则", path: "" },
        { name: "营探平台诚信保证金管理规定", path: "" },
        { name: "营探搜索排名规则", path: "" },
        { name: "营探网络安全事件应急预案", path: "" },
        { name: "营探信用评价规则", path: "" },
      ];
      $(".left").css("width", "320px");
      $(".left").css("font-size", "15px");
    },
  },
  computed: {
    getAccount() {
      // return this.cookieOperations.getCookie('loginName');
      return window.localStorage.getItem("userName");
    },
  },
  components: {
    TabNavigation,
  },
};
</script>

